import axios from "axios";
import { API_BASE_URL } from "@/utils/api";
const token = localStorage.getItem("authToken");

export const fetchWindows = (branch_id)=>{
    try {
        const result = axios.get(
            `${API_BASE_URL}/windows/${branch_id}`,
            {
              headers: {
                bearer: token,
              },
            }
          );
          return result;
        
    } catch (error) {
        console.log(error)
    }
    
}