import axios from "axios";
import { API_BASE_URL } from "@/utils/api";

const token = localStorage.getItem("authToken");

export const fetchTicketListByBranchId = async (branch) => {
  let list = [];
  try {
    list = await axios.get(`${API_BASE_URL}/tickets/list/${branch.branchId}`, {
      headers: {
        bearer: token,
      },
    });
  } catch (err) {
    console.log(err);
  }
  return list.data.data.tickets;
};

export const fetchBranchTickets = async () => {
  try {
    const result = await axios.get(`${API_BASE_URL}/tickets/branch-tickets`, {
      headers: {
        bearer: token,
      },
    });
    return result;
  } catch (err) {
    console.log(err);
  }
};

export const fetchTicketList = async () => {
  try {
    const result = await axios.get(`${API_BASE_URL}/tickets/list`, {
      headers: {
        bearer: token,
      },
    });
    return result;
  } catch (err) {
    console.log(err);
  }
};
