import axios from "axios";
import { API_BASE_URL } from "@/utils/api";
const token = localStorage.getItem("authToken");

export const fetchMap = async(branch_id)=>{
    const url = branch_id ? `${API_BASE_URL}/tickets/map/${branch_id}` : `${API_BASE_URL}/tickets/map`;
    try{
        const result = await axios.get(url,
            {
              headers: {
                bearer: token
              },
            }
          );
          return result;
    }catch(err){
        console.log(err);
    }
}