<script setup>
import { onMounted, ref, computed, watch } from "vue";

import { useRoute } from "vue-router";

import { fetchBranchTickets, fetchTicketList, fetchTicketListByBranchId } from "@/utils/dashboardAmount";

const route = useRoute();



const totalTickets = ref(0);

const isChild = ref(false);

const search = ref("");
const headers = [
  {
    align: "center",
    key: "iin",
    sortable: false,
    title: "",
  },
  { align: "center", key: "ticketno", title: " " },
  { align: "center", key: "servicename", title: " " },
  { align: "center", key: "idbranch", title: "" },
  { align: "center", key: "operator", title: "" },
  { align: "center", key: "state", title: "" },
  { align: "center", key: "starttime", title: " " },
  // { key: "waittime", title: " " },
  { align: "center", key: "startservtime", title: " " },
  { align: "center", key: "rating", title: "" },
  { align: "center", key: "waitover", title: "  " },
  { align: "center", key: "servover", title: "  " },
];
const desserts = ref([]);
const parentList = ref([]);

const branchTickets = ref([]);
const series = ref([]);
const categories = ref([]);

const chartOptions = ref({
  chart: {
    type: "bar",
    height: 350,
    stacked: true,
    toolbar: {
      show: true,
    },
    zoom: {
      enabled: true,
    },
    events: {
      legendClick: function (chartContext, seriesIndex, config) {
        console.log(chartContext, seriesIndex, config);
      },
    },
    // events: {
    //   dataPointSelection: function(event, chartContext, config) {
    //     console.log(event,chartContext,config)
    //   },
    //   legendClick: function(chartContext, seriesIndex, config) {
    //     console.log(seriesIndex,chartContext,config)
    //   }
    // },
  },

  colors: ["#2ecc71", "#3498db", "#f39c12", "#d35400", "#c0392b"],
  responsive: [
    {
      breakpoint: 480,
      options: {
        legend: {
          position: "top",
          offsetX: -10,
          horizontalAlign: 'center', // Align legend items horizontally at the center
          offsetY: -10,
        },
      },
    },
  ],
  plotOptions: {
    bar: {
      horizontal: false,
      borderRadius: 10,
      dataLabels: {
        total: {
          enabled: true,
          style: {
            fontSize: "13px",
            fontWeight: 900,
          },
        },
      },
    },
  },
  xaxis: {
    type: "string",
    categories: categories.value,
    // categories: ["<a href='/tickets'> 1991</a>", 1992, 1993, 1994, 1995, 1996, 1997, 1998],
  },
  legend: {
    position: "top",
    offsetY: 0,
  },
  fill: {
    opacity: 1,
  },
});

const apexChart = ref(null);

const updateChartByBranchId = async (branch) => {
  try {
    categories.value = branch.children.map((e) => e.branchName);

    series.value = [
      {
        name: "",
        data: branch.children.map((item) => item.stateTickets.COMPLETED),
      },
      {
        name: "",
        data: branch.children.map((item) => item.stateTickets.INSERVICE),
      },
      {
        name: "",
        data: branch.children.map((item) => item.stateTickets.NEW),
      },
      {
        name: " ",
        data: branch.children.map((item) => item.stateTickets.MISSED),
      },
      {
        name: "Alarm",
        data: branch.children.map((item) => item.stateTickets.ALARM),
      },
    ];
    updateChart();
  } catch (err) {
    console.log(err)
  }

};

const updateTicketByBranchId = async (branch) => {
  try {
    desserts.value = await fetchTicketListByBranchId(branch);
  } catch (err) {
    console.log(err);
  }
};



const handleBarClick = async (event, chartContext, config) => {
  const point = config.dataPointIndex;
  const branch = branchTickets.value[point];
  try {
    if (branch.children && !isChild.value) {
      updateChartByBranchId(branch);
      isChild.value = true;
      updateTicketByBranchId(branch);
    }
  } catch (err) {
    console.log(err);
  }
  // console.log(branch);


  // Add your custom logic here for handling bar clicks
};

const updateChart = () => {
  apexChart.value.updateOptions({
    xaxis: {
      categories: categories.value,
    },
    series: [
      {
        data: series.value,
      },
    ],
  });
};
const backChart = () => {
  try {
    categories.value = branchTickets.value.map((e) => e.branchName);
    series.value = [
      {
        name: "",
        data: branchTickets.value.map((item) => item.stateTickets.COMPLETED),
      },
      {
        name: "",
        data: branchTickets.value.map((item) => item.stateTickets.INSERVICE),
      },
      {
        name: "",
        data: branchTickets.value.map((item) => item.stateTickets.NEW),
      },
      {
        name: " ",
        data: branchTickets.value.map((item) => item.stateTickets.MISSED),
      },
      {
        name: "Alarm",
        data: branchTickets.value.map((item) => item.stateTickets.ALARM),
      },
    ];
    isChild.value = false;
    updateChart();
    desserts.value = parentList.value;
    if (route.query.branch_id) {
      const newUrl = window.location.protocol + '//' + window.location.host + window.location.pathname;
      history.replaceState({}, document.title, newUrl);
    }
  } catch (err) {
    alert(err);
  }
};

const getBranchTickets = async () => {
  try {
    
    const result = await fetchBranchTickets();
    branchTickets.value = result.data.data;
    totalTickets.value = result.data.count;

    if (route.query.parent_branch) {
      // console.log("route", route.query.branch_id);
      // console.log(branchTickets.value);
      const propBranch = branchTickets.value.find(e => e.branchId === route.query.parent_branch);
      // console.log(propBranch);
      updateChartByBranchId(propBranch);
      updateTicketByBranchId(propBranch);
      isChild.value = true;
    }
    else
      backChart();
  } catch (err) {
    console.log(err);
  }
};

const getTicketList = async () => {
  try {
    const result = await fetchTicketList();
    // console.log(result.data.data.tickets);
    parentList.value = result.data.data.tickets;
    desserts.value = result.data.data.tickets;
  } catch (err) {
    console.log(err);
  }
  // console.log(desserts);
};

const formattedDesserts = computed(() => {
  return desserts.value.map(ticket => {
    return {
      ...ticket,
      starttime: new Date(ticket.starttime).toLocaleString("ru-RU"),
      startservtime: new Date(ticket.startservtime).toLocaleString("ru-RU"),
      rating: ticket.rating === "5" ? "" : ticket.rating === "4" ? "" : ticket.rating === "4" ? "" : " ",
      waitover: ticket.waitover === "true" ? "" : "",
      servover: ticket.servover === "true" ? "" : "",
      state: ticket.state === "COMPLETED" ? "" : ticket.state === "NEW" ? "" : ticket.state === "INSERVICE" ? "" : "",
      idbranch: ticket.idbranch
    }
  }).sort((a, b) => {
    return a.starttime - b.starttime;
  });
});

watch(() => route.query, () => {
  getBranchTickets();
});

onMounted(() => {
  getBranchTickets();
  getTicketList();
  // console.log(route.query.branch_id);
  // if(route.query.branch_id){
  //   updateByProps(route.query.branch_id);
  // }

  // console.log(apexChart.value)
});
</script>


<template>
  <div class="amount-container">

    <h3>{{ totalTickets }}</h3>
    <div class="chartBlock">

      <v-btn @click="backChart()" v-if="isChild" class="back m-4"><i class="fas fa-arrow-left fa-2xl"></i></v-btn>
      <div class="chart p-4">
        <apexchart ref="apexChart" height="500" :options="chartOptions" :series="series"
          @dataPointSelection="handleBarClick"></apexchart>
      </div>

    </div>
    <div class="tickets">
      <v-card title="" flat>
        <!-- <template v-slot:text>
          <v-text-field
            v-model="search"
            label="Search"
            prepend-inner-icon="mdi-magnify"
            variant="outlined"
            hide-details
            single-line
          ></v-text-field>
        </template> -->

        <v-data-table :headers="headers" :items="formattedDesserts" :search="search" no-data-text="  "
          items-per-page-text="  "></v-data-table>
      </v-card>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.chartBlock {
  width: 100%;

  .back {
    border-radius: 1rem;
  }
}

