const host = process.env.VUE_APP_SERVER_HOST;

const protocol = process.env.VUE_APP_BACK_PROTOCOL;

const port = protocol === "http" ? process.env.VUE_APP_SERVER_PORT : process.env.VUE_APP_HTTPS_PORT;

export const API_BASE_URL = `${protocol}://${host}:${port}/api/v1`;
// console.log(API_BASE_URL)
export const ROOT_URL = `${protocol}://${host}:${port}`;

export const BOARD_API_URL = `${API_BASE_URL}/board`;
export const TICKET_API_URL = `${API_BASE_URL}/tickets`;
export const BRANCHES_API_URL = `${API_BASE_URL}/branches`;
export const AUTH_API_URL = `${API_BASE_URL}/auth`;
