import axios from "axios";
import { API_BASE_URL } from "@/utils/api";

const token = localStorage.getItem("authToken");

export const fetchMessages = async () => {
    try {
        const result = await axios(`${API_BASE_URL}/messages`, {
            headers: {
                bearer: token,
            },
        });
        const resultMessages = result.data.data.message;
        resultMessages.map((e) => {
            const d = new Date(e.created_at);
            e.created_at =
                d.toLocaleDateString("RU-ru") +
                " " +
                d.getHours() +
                ":" +
                d.getMinutes() +
                ":" +
                d.getSeconds();
        });
        return resultMessages;
    } catch (err) {
        console.log(err);
    }
};

export const fetchUsers = async()=>{
    const result = await axios.get(`${API_BASE_URL}/users/users/last`, {
    headers: {
      bearer: token,
    },
  });
  const value = result.data.data.users;
  return value;
}

export const postMessage = async(message)=>{
    try {
        await axios.post(`${API_BASE_URL}/messages`, {
         txt: message,
       }, {
         headers: {
           bearer: token,
         },
       });
      return true;
       // console.log(result)
     } catch (err) {
       console.log(err);
       return false;
     }
}
