import axios from "axios";
import { BRANCHES_API_URL } from "@/utils/api";

export const getBranch = async (value) => {
  const url = value ? `${BRANCHES_API_URL}?type=${value}` : BRANCHES_API_URL;
  const token = localStorage.getItem("authToken");
  try {
    const result = await axios.get(url, {
      headers: {
        bearer: token,
      },
    });
    //   console.log(result)
    return result.data.rows;
  } catch (err) {
    console.log(err);
  }
};
