import axios from "axios";
import { API_BASE_URL } from "@/utils/api";

const token = localStorage.getItem("authToken");

export const fetchTicketList = async (
  pageNumber,
  value,
  parent_branch,
  branch_id
) => {
  try {
    let params = new URLSearchParams({
      page: pageNumber,
      limit: 100,
      filter:
        value === "bad-rate"
          ? "bad"
          : value === "wait"
          ? "wait"
          : value === "serv"
          ? "serv"
          : undefined,
    });
    if (!value) {
      params = new URLSearchParams({
        page: pageNumber,
        limit: 100,
      });
    }

    let url = branch_id
      ? `${API_BASE_URL}/tickets/list/${branch_id}`
      : parent_branch
      ? `${API_BASE_URL}/tickets/list/${parent_branch}`
      : `${API_BASE_URL}/tickets/list`;

    url += `?${params.toString()}`;

    const result = await axios.get(url, {
      headers: { bearer: token },
    });

    return result;
  } catch (err) {
    console.log(err);
  }
};
export const downloadPage = async () => {
  const URL = `${API_BASE_URL}/tickets/download`;

  try {
    const response = await axios.get(URL, {
      headers: {
        bearer: token,
      },
      responseType: "blob",
    });
    const url = window.URL.createObjectURL(new Blob([response.data]));
    const link = document.createElement("a");
    link.href = url;
    link.setAttribute("download", "generated_data.xlsx");
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  } catch (err) {
    console.log(err);
  }
};
